<template>
  <svg :class="svgClass" aria-hidden="true">
    <use :xlink:href="iconName"></use>
  </svg>
</template>

<script>
export default {
  name: 'svg-icon',
  props: {
    iconClass: {
      type: String,
      required: true
    },
    className: {
      type: String
    }
  },
  mounted() {
  },
  computed: {
    iconName() {
      return `#icon-${  this.iconClass}`;
    },
    svgClass() {
      if (this.className) {
        return `svg-icon ${  this.className}`;
      } else {
        return 'svg-icon';
      }
    }
  }
};
</script>

<style scoped>
.svg-icon {
  width: 1em;
  height: 1em;
  vertical-align: middle;
  fill: currentColor;
  overflow: hidden;
  font-size: 18px;
  cursor: pointer;
}
</style>
